img {
  width: 204px;
  height: 204px;
  border: 0.5px solid black;
  box-shadow: 2px 2px 2px 2px rgb(214, 213, 213);
  margin: 2px;
}

.container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 8px;
  margin: 5px;
  font-size: 15px;
}

.storeItem {
  box-shadow: 2px 2px 2px 2px rgb(214, 213, 213);
  text-align: center;
  padding: 3px;
}

span::first-letter {
  text-transform: uppercase;
}

.item-link {
  color: black;
  text-decoration: none;
}
